import React, { Component } from 'react';
import moment from 'moment';
import Calendar from 'react-calendar';
import Button from 'components/button';
import {
  ButtonWrap,
  Calendar as CalendarWrap,
  SelectWrap,
  OvSelect,
} from './Calendar.css';

export default class CalendarComponent extends Component {
  state = {};
  render() {
    const {
      selectSeats,
      events,
      startDates,
      endDates,
      startDate,
      endDate,
      changeEndDate,
      subTitle,
      isLoading,
    } = this.props;

    const range =
      startDate.value && endDate.value
        ? {
            value: [
              moment(startDate.value, 'YYYY-MM-DD').toDate(),
              moment(endDate.value, 'YYYY-MM-DD').toDate(),
            ],
          }
        : {};

    return (
      <CalendarWrap>
        <SelectWrap>
          <div>
            <label htmlFor="datepicker-from" className="select-label">
              From
            </label>
            <OvSelect
              value={startDate}
              options={startDates}
              onChange={changeEndDate}
              id="datepicker-from"
              className="select-container"
              classNamePrefix="select"
            />
          </div>
          <div>
            <label htmlFor="datepicker-to" className="select-label">
              To
            </label>
            <OvSelect
              value={endDate}
              options={endDates}
              id="datepicker-to"
              className="select-container"
              classNamePrefix="select"
            />
          </div>
        </SelectWrap>
        <Calendar className="calendar" selectRange {...range} />
        <ButtonWrap>
          <Button
            isLoading={isLoading}
            type="primary"
            size="medium"
            className="is-full-width"
            onClick={() => selectSeats(startDate)}
          >
            {subTitle}
          </Button>
        </ButtonWrap>
      </CalendarWrap>
    );
  }
}
