import styled from 'styled-components';
import MEDIA from 'helpers/mediaTemplates';

export const ArticleImage = styled.div`
  width: 40%;
  min-height: 300px;
  margin-right: 2rem;
  transition: all linear 250ms;
  &:hover {
    cursor: pointer;
    filter: opacity(.6);
  }
  .gatsby-image-wrapper {
    height: 100%;
  }
  ${MEDIA.PHONE`
    width: 100%;
    min-height: auto;
  `};
`;
export const Section = styled.div`
  display: flex;
  box-shadow: 0 12px 16px rgba(0, 0, 0, .07);
  border-radius: 5px;
  overflow: hidden;
  height: 100%;

  .article-text {
    width: 50%;
    color: #707070;
  }
  ${MEDIA.DESKTOP`
    margin-bottom: 4rem;
    width: 100%;
  `};
  ${MEDIA.PHONE`
    display: block;
    font-size: 1.4rem;

    .article-text {
      padding: 0 3rem 2rem;
      width: auto;
    }
  `};
`;

export const Button = styled.button`
  display: flex;
  align-items: center;
  color: #FF4C00;
  font-size: 2rem;
  font-weight: Bold;
  padding: 1rem 4rem;
  border: 1px solid #FF4C00;
  background-color: white;
  outline: none;
  cursor: pointer;
  ${({ btnBorder }) => () => {
    switch (btnBorder) {
      case true:
        return 'border-radius: 4rem;';
      default:
        return 'border-radius: 5px;';
    }
  }}
  transition: all 0.4s ease-in-out;
  &:hover {
    background-color: #FF4C00;
    color: white;
  }
`;

export const Title = styled.span`
  display: block;
  font-size: 3.2rem;
  font-weight: 500;
  color: #5D5D5D;
  cursor: pointer;
  margin: 2rem 0;
  ${MEDIA.TABLET`
    font-size: 2.5rem;
  `};

`;
