import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  CarouselProvider,
  Slider,
  Slide,
  ButtonBack,
  ButtonNext,
  WithStore,
} from 'pure-react-carousel';
import Img from 'gatsby-image';

import { CarouselWrapper, Carousel, ImageWrapper } from './carousel.css';

class CarouselComponent extends Component {
  setCenteredElClass(index) {
    const { carouselStore } = this.props;
    const { currentSlide } = carouselStore;

    return index === currentSlide + 1 ? 'centered' : '';
  }

  render() {
    const { images, carouselStore } = this.props;
    const { currentSlide } = carouselStore.getStoreState();

    return (
      <Carousel>
        <ButtonBack />
        <ButtonNext />
        <Slider classNameAnimation="animation">
          {images.map((image, index) => (
            <Slide
              index={index}
              // eslint-disable-next-line react/no-array-index-key
              key={index}
              classNameVisible={`${
                index === currentSlide + 1 ? 'centered' : 'visible'
              }`}
            >
              <ImageWrapper>
                <Img
                  fluid={image ? image.childImageSharp.fluid : {}}
                  alt="fsds"
                />
              </ImageWrapper>
            </Slide>
          ))}
        </Slider>
      </Carousel>
    );
  }
}

CarouselComponent.propTypes = {
  images: PropTypes.arrayOf(PropTypes.object).isRequired,
  carouselStore: PropTypes.shape({
    currentSlide: PropTypes.number,
  }).isRequired,
};

const CarouselWithStore = WithStore(CarouselComponent, state => ({
  currentSlide: state.currentSlide,
}));

class Wrap extends Component {
  state = { slidesAmount: 3 };

  componentDidMount() {
    const slidesAmount = window && window.innerWidth <= 767 ? 1 : 3;

    this.setState({
      slidesAmount,
    });
  }

  render() {
    const { slidesAmount } = this.state;
    const { images } = this.props;

    return (
      <CarouselWrapper>
        <CarouselProvider
          naturalSlideWidth={467}
          naturalSlideHeight={311}
          visibleSlides={slidesAmount}
          currentSlide={0}
          dragEnabled={false}
          totalSlides={images.length}
        >
          <CarouselWithStore {...this.props} />
        </CarouselProvider>
      </CarouselWrapper>
    );
  }
}

Wrap.propTypes = {
  images: PropTypes.arrayOf(PropTypes.object).isRequired,
};

export default Wrap;
