import React from 'react';
import PropTypes from 'prop-types';
import { Button as StyledButton } from './button.css';

const Button = ({ children, type, size, className, onClick, isLoading }) => (
  <StyledButton
    disabled={isLoading}
    onClick={onClick}
    className={className}
    type={type}
    size={size}
  >
    {isLoading ? 'loading...' : children}
  </StyledButton>
);

Button.defaultProps = {
  onClick: () => {},
  type: false,
  size: 'large',
  className: null,
  isLoading: false,
};

Button.propTypes = {
  children: PropTypes.node.isRequired,
  onClick: PropTypes.func,
  type: PropTypes.string,
  size: PropTypes.string,
  className: PropTypes.string,
  isLoading: PropTypes.bool,
};

export default Button;
