import React, { Component, Fragment } from 'react';
import Button from 'components/button';
import { EventWrap, Modal, Input } from '../Event/Event.css';
import { ButtonWrap, OvSelect } from '../Calendar/Calendar.css';

export default class BookingTable extends Component {
  state = {};

  submitUserInfo = items => {
    const isValid = this.form.checkValidity();

    if (!isValid) {
      return;
    }

    const data = items.map(item => ({
      id: item.define.field_id,
      value: this[item.define.field_id].value,
    }));

    this.props.handleSubmit(data);
  };

  render = () => {
    const { data, onBack, isLoading } = this.props;
    const items = Object.values(data);

    return (
      <EventWrap>
        <Modal>
          <div className="block-title">
            <a onClick={onBack} href="#" className="back-btn">
              {'Back'}
            </a>
          </div>
          <div className="modal-content">
            <form
              ref={form => {
                this.form = form;
              }}
              className="modal-article__text"
            >
              {items.map(item => {
                const { define, value } = item;
                const { layout, field_id, required } = define;
                const { type, lbl } = layout;

                if (type === 'select') {
                  const { options } = layout;
                  const processedOpts = Object.keys(options).map(key => ({
                    label: options[key],
                    value: key,
                  }));

                  const val = processedOpts.find(
                    ({ value: val }) => val === value
                  );

                  return (
                    <Fragment key={field_id}>
                      <OvSelect
                        className="select-container table-pricing"
                        classNamePrefix="select"
                        placeholder={lbl}
                        ref={select => {
                          if (select === null) {
                            return;
                          }
                          this[field_id] = select;
                        }}
                        defaultValue={val}
                        options={processedOpts}
                        required={!!required}
                      />
                      <div className="required-lbl">
                        {lbl} current field is required
                      </div>
                    </Fragment>
                  );
                }

                if (type === 'text') {
                  return (
                    <Fragment key={field_id}>
                      <Input
                        type="text"
                        ref={input => {
                          if (input === null) {
                            return;
                          }
                          this[field_id] = input;
                        }}
                        placeholder={lbl}
                        className="success"
                        defaultValue={value}
                        required={!!required}
                      />
                      <div className="required-lbl">
                        {lbl} current field is required
                      </div>
                    </Fragment>
                  );
                }

                return <div key={field_id}>item</div>;
              })}
            </form>
          </div>
          <ButtonWrap>
            <Button
              onClick={() => this.submitUserInfo(items)}
              type="primary"
              size="medium"
              className="is-full-width"
              isLoading={isLoading}
            >
              Proceed to checkout
            </Button>
          </ButtonWrap>
        </Modal>
      </EventWrap>
    );
  };
}
