export const Calendar = styled.div`
  position: relative;
  background-color: #fff;
  border-radius: 5px;
  overflow: hidden;

  .react-calendar {
    pointer-events: none;
    width: 30rem;
    border: 0;
    &__month-view {
      padding: 2rem;
      &__days {
        &__day {
          &--weekend {
            color: #000;
          }
          &--neighboringMonth {
            color: #969696;
          }
        }
      }
    }
    &__tile {
      padding: 8px 0;
      margin: 0.3rem 0;
      position: relative;
      &:nth-child(7n) {
        border-top-right-radius: 100%;
        border-bottom-right-radius: 100%;
      }
      &:nth-child(7n + 1) {
        border-top-left-radius: 100%;
        border-bottom-left-radius: 100%;
      }
      &--rangeStart {
        border-top-left-radius: 100%;
        border-bottom-left-radius: 100%;
      }
      &--rangeEnd,
      &--hasActive{
        border-top-right-radius: 100%;
        border-bottom-right-radius: 100%;
      }
      &--rangeBothEnds {
        border-radius: 100%;
      }
      &--active,
      &--hasActive {
        background-color: #e3e0d5;
        color: #000;
        &:enabled:hover,
        &:enabled:focus {
          background-color: #e3e0d5;
        }
      }
      &--hover,
      &--hover ~ button:hover,
      &--rangeEnd ~ button:hover,
      &--rangeBothEnd ~ button:hover,
      &--&--hasActive ~ button:hover {
        border-radius: 100%;
      }
      &--rangeStart::before,
      &--rangeEnd::before,
      &--hasActive::before,
      &--rangeBothEnd::before {
        content: '';
        position: absolute;
        border: 2px solid #fff;
        bottom: 0;
        top: 0;
        left: 0;
        right: 0;
        border-radius: 100%;
      }
    }
  }
  .react-calendar__navigation {
    margin-bottom: 6rem;
    height: 5.4rem;
    background-color: #f9f9f6;
    width: 100%;
    overflow: hidden;
    &__label {
      font-size: 1.5rem;
    }
    &__arrow {
      padding: 0;
      font-size: 3rem;
      color: #bababa;
    }
  }
  color: grey;

  .react-calendar__month-view__weekdays__weekday {
    color: grey;
  }
`;

