import React, { Component } from 'react';
import PropTypes from 'prop-types';
import axios from 'axios';
import moment from 'moment';
import Mark from 'components/mark';
import Clock from 'images/ico_clock.png';
import Calendar from './Calendar';
import Event from './Event';
import BookingTable from './BookingTable';
import Button from 'components/button';
import { ButtonWrap } from './Calendar/Calendar.css';
import { Section, Title, BookingText } from './bookingDescription.css';
import { EventWrap, Modal, Input } from './Event/Event.css';

const domain = 'https://theoutdoorvoyageapi.herokuapp.com';

class BookingDescription extends Component {
  state = {
    events: [{}],
    isSelectDates: true,
    isEventDetails: false,
    isClientInfo: false,
    isBraintree: false,
    eventsDetails: [{}],
    startDate: { label: null, value: null },
    startDates: [],
    endDate: { label: null, value: null },
    endDates: [],
    bookingSessionId: null,
    instance: null,
    isLoading: false,
  };

  findEndDate = (events, startDate) => {
    const event = events.find(e => e.start_date === startDate.value);
    const endDate = {
      label: moment(event.end_date).format('l'),
      value: event.end_date,
    };

    return endDate;
  };

  changeEndDate = startDate => {
    const events = this.state.events;
    const endDate = this.findEndDate(events, startDate);

    this.setState({
      startDate,
      endDate,
      endDates: [endDate],
    });
  };

  selectSeats = () => {
    this.setState({
      isSelectDates: false,
      isEventDetails: true,
      isClientInfo: false,
      isBraintree: false,
    });
  };

  submitEvent = async (e, opts) => {
    const params = opts.reduce(
      (result, param) =>
        param.value === 0 ? result : { ...result, [param.title]: param.value },
      {}
    );

    this.setState({ isLoading: true });

    const { data: bookingSessionId } = await axios.get(
      `${domain}/api/v1/events/session-id`,
      {
        params: {
          productId: this.props.productId,
          startDate: this.state.startDate.value,
          endDate: this.state.endDate.value,
          ...params,
        },
      }
    );

    console.log('bookingSessionId', bookingSessionId);

    this.setState({
      bookingSessionId,
      isSelectDates: false,
      isEventDetails: false,
      isClientInfo: true,
      isBraintree: true,
      isLoading: false,
    });
  };

  initializeBraintree = async braintree => {
    if (!braintree) {
      return {};
    }

    const { data: token } = await axios.get(
      `${domain}/api/v1/braintree/client_token`
    );

    const instance = await braintree.dropin.create({
      authorization: token,
      container: '#dropin-container',
    });

    this.setState({ instance, isClientInfo: false });
  };

  handleSubmitPayment = async () => {
    const data = await this.state.instance.requestPaymentMethod();
    const result = await axios({
      method: 'post',
      url: `${domain}/api/v1/braintree/checkout`,
      data: {
        nonce: data.nonce,
      },
    });

    // console.log('result!!! ', result);
  };

  async componentDidMount() {
    const { data } = await axios.get(`${domain}/api/v1/events/events`, {
      params: {
        productId: this.props.productId,
      },
    });

    const events = Object.values(data);

    const isEventsExist = !!events[0];

    if (!isEventsExist) {
      return;
    }

    const startDates = events.map(({ start_date }) => ({
      label: moment(start_date).format('l'),
      value: start_date,
    }));

    const startDate = startDates[0];

    const endDate = this.findEndDate(events, startDate);

    this.setState({
      events,
      startDates,
      startDate,
      endDate,
      endDates: [endDate],
      isLoading: true,
    });

    const eventsPromises = events.map(({ start_date, end_date }) =>
      axios
        .get(`${domain}/api/v1/events/event-details`, {
          params: {
            productId: this.props.productId,
            start_date,
            end_date,
          },
        })
        .then(({ data }) => data)
    );

    const eventsDetails = await Promise.all(eventsPromises);

    this.setState({
      eventsDetails,
      isLoading: false,
    });

    const userFormContent = await axios
      .get(`${domain}/api/v1/events/user-form`)
      .then(({ data }) => data.booking_form_ui);

    this.setState({
      userFormContent,
    });
  }

  bookEvent = async tableData => {
    const processedUserParams = tableData.reduce(
      (result, param) =>
        !param.value ? result : { ...result, [`${param.id}`]: param.value },
      {}
    );

    const session_id = this.state.bookingSessionId;

    this.setState({ isLoading: true });

    const { data } = await axios.get(`${domain}/api/v1/events/book`, {
      params: {
        session_id,
        ...processedUserParams,
      },
    });

    if (typeof window !== 'undefined') {
      await this.initializeBraintree(window.braintree);
    }

    this.setState({
      isLoading: false,
    });
  };

  goToCalendar = e => {
    e.preventDefault();

    this.setState({
      isSelectDates: true,
      isEventDetails: false,
      isClientInfo: false,
      isBraintree: false,
      instance: null,
    });
  };

  goToEvent = e => {
    e.preventDefault();

    this.setState({
      isSelectDates: false,
      isEventDetails: true,
      isClientInfo: false,
      isBraintree: false,
    });
  };

  goToUserInfo = e => {
    e.preventDefault();

    if (this.dropinContainer) {
      this.dropinContainer.innerHTML = '';
    }

    this.setState({
      isSelectDates: false,
      isEventDetails: false,
      isClientInfo: true,
      instance: null,
    });
  };

  render() {
    const { title, children, subTitle } = this.props;
    const {
      events,
      startDates,
      endDates,
      startDate,
      endDate,
      isSelectDates,
      eventsDetails,
      isEventDetails,
      isClientInfo,
      userFormContent,
      instance,
      isBraintree,
      isLoading,
    } = this.state;

    const daysDiff = Math.floor(endDate.value - startDate.value);

    const daysAdmount = daysDiff > 0 ? daysDiff + 1 : 0;

    return (
      <Section>
        <BookingText>
          <Title as={'h3'}>{title}</Title>
          <section>
            {!!daysAdmount && (
              <Mark>
                <img width="17px" src={Clock} alt="f" />
                <span>{daysAdmount} days</span>
              </Mark>
            )}
          </section>
          {children}
        </BookingText>

        {(isSelectDates || isEventDetails || isClientInfo || isBraintree) && (
          <div className="flex-wrap">
            {isSelectDates && (
              <Calendar
                startDates={startDates}
                endDates={endDates}
                startDate={startDate}
                endDate={endDate}
                events={events}
                selectSeats={this.selectSeats}
                subTitle={subTitle}
                changeEndDate={this.changeEndDate}
                isLoading={isLoading}
              />
            )}
            {isEventDetails && (
              <Event
                startDates={startDates}
                endDates={endDates}
                startDate={startDate}
                endDate={endDate}
                events={eventsDetails}
                subTitle={subTitle}
                changeEndDate={this.changeEndDate}
                submitEvent={this.submitEvent}
                onBack={this.goToCalendar}
                isLoading={isLoading}
              />
            )}
            {isClientInfo && (
              <BookingTable
                handleSubmit={this.bookEvent}
                data={userFormContent}
                onBack={this.goToEvent}
                isLoading={isLoading}
              />
            )}
            {isBraintree && (
              <EventWrap>
                <Modal>
                  <div className="block-title">
                    <a onClick={this.goToUserInfo} href="#" className="back-btn">
                      Back
                    </a>
                  </div>
                  <div className="modal-content">
                    <div
                      id="dropin-container"
                      ref={container => {
                        if (container === null) {
                          return;
                        }
                        this.dropinContainer = container;
                      }}
                    />
                  </div>
                  {instance && (
                    <ButtonWrap>
                      <Button type="primary" size='small' onClick={this.handleSubmitPayment}>Submit</Button>
                      <Button type="primary" size='small' onClick={this.goToCalendar}>
                        Make other booking
                      </Button>
                    </ButtonWrap>
                  )}
                </Modal>
              </EventWrap>
            )}
          </div>
        )}
      </Section>
    );
  }
}

BookingDescription.propTypes = {
  title: PropTypes.string.isRequired,
  persons: PropTypes.number.isRequired,
  days: PropTypes.number.isRequired,
  children: PropTypes.node.isRequired,
  productId: PropTypes.number.isRequired,
};

export default BookingDescription;
