import styled from 'styled-components';
import MEDIA from 'helpers/mediaTemplates';

export const Text = styled.span`

  ${({ size }) => () => {
    switch (size) {
      case 'large':
        return 'font-weight: 600;' +
               'font-size: 6rem;';
      case 'medium':
        return 'font-weight: 600;' +
               'font-size: 3.2rem;';
      default:
        return 'font-weight: 500;' +
               'font-size: 2rem;';
    }
  }};
  line-height: 1.4;


  ${MEDIA.DESKTOP`
    ${({ size }) => () => {
      switch (size) {
        case 'large':
          return 'font-size: 5.5rem';
        case 'medium':
          return 'font-weight: 600;' +
                 'font-size: 3.2rem;';
        default:
          return '2rem';
      }
    }};
     &::before {
      font-size: 10vw;
     }
  `};
  ${MEDIA.TABLET`
  &::before {
      display: none;
     }
    ${({ size }) => () => {
      switch (size) {
        case 'large':
          return 'font-size: 4rem';
        case 'medium':
          return 'font-weight: 600;' +
                 'font-size: 2.9rem;';
        default:
          return '2rem';
      }
    }};
  `};
  ${MEDIA.PHONE`

      &::before {
          display: none;
         }
        ${({size}) => () => {
            switch (size) {
              case 'large':
                return 'font-size: 2.9rem';
              case 'medium':
                return 'font-weight: 600;' +
                  'font-size: 2.4rem;';
              default:
                return '2rem';
            }
          }
        };
  `};
`;
