import React from 'react';
import PropTypes from 'prop-types';
import { Text } from './title.css';

const Title = ({ children, as, size }) => (
    <Text as={as} size={size}>
      {children}
    </Text>
  );

Title.defaultProps = {
  as: 'span',
  size: null,
};

Title.propTypes = {
  as: PropTypes.string,
  size: PropTypes.string,
};

export default Title;
