import styled from 'styled-components';
import MEDIA from 'helpers/mediaTemplates';

export const Button = styled.button`
  display: inline-block;
  border: 0;
  border-radius: 5px;
  padding: 10px 30px;
  ${({ type }) => () => {
    switch (type) {
      case 'primary':
        return 'color: #fff;' + 'background-color: #FF4C00;';
      case 'secondary':
        return 'color: #414141;' + 'background-color: #E3E0D5;';
      default:
        return 'color: #FF4C00;' + 'background-color: #fff;';
    }
  }};
  ${({ size }) => () => {
    switch (size) {
      case 'large':
        return (
          'font-size: 2.8rem;' +
          'font-weight: 600;' +
          'text-transform: uppercase;'
        );
      case 'medium':
        return 'font-size: 1.6rem;';
      case 'small':
        return 'font-size: 1.4rem;' +
               'padding: 10px;';
      default:
        return (
          'font-size: 1.6rem;' + 'font-weight: 400;' + 'text-transform: normal;'
        );
    }
  }};
  & + & {
    margin-left: 1rem;
  }
  &:disabled {
    background-color: grey;
  }
  &.is-centered {
    margin: 0 auto;
  }
  &.is-full-width {
    width: 100%;
  }
  &:hover {
    cursor: pointer;
    box-shadow: inset 0 0 100px 100px rgba(0, 0, 0, 0.2);
  }
  ${MEDIA.TABLET`
    ${({ size }) => () => {
      switch (size) {
        case 'large':
          return 'font-size: 1.6rem;';
        case 'medium':
          return 'font-size: 1.4rem';
        case 'small':
          return 'font-size: 1.3rem';
        default:
          return 'font-size: 1.4rem;';
      }
    }};
  `};
`;
