import React, { Component, Fragment } from 'react';
import { ParamWrap, ParamContent } from './Parameter.css';

export default class Event extends Component {
  reduceAmount = ({ title, min }) => {
    const { changeParamValue, param } = this.props;
    const { value } = param;

    if (value <= min) {
      return;
    }

    changeParamValue({ title, value: value - 1 });
  };

  enhanceAmount = ({ title, max, lock, available }) => {
    const { param, changeParamValue } = this.props;
    const { value } = param;

    if (
      (max !== 0 && value === max) ||
      (max === 0 && lock === 1 && value === available)
    ) {
      return;
    }

    changeParamValue({ title, value: value + 1 });
  };

  getMaximum({ lock, available, max }) {
    if (lock === 1 && available && max !== max && max < available) {
      return max;
    }

    if (lock === 1 && available && max !== max && max > available) {
      return available;
    }

    if (max) {
      return max;
    }

    return false;
  }

  render() {
    const { param, available } = this.props;
    const {
      MIN: min,
      MAX: max,
      lock,
      lbl,
      calculatedPrice,
      title,
      value,
      days,
    } = param;

    const maximum = this.getMaximum({ lock, available, max });
    const currAvailable = maximum - param.value;

    return (
      <ParamWrap>
        <div className="param-text-cont">
          <p className="param-title">{lbl}</p>
          {maximum && (
            <p className="seats-number-count">{currAvailable} available</p>
          )}
        </div>

        <ParamContent key={lbl}>
          <div className="centered-cont">
            <button
              onClick={() => this.reduceAmount({ title, min })}
              className="counter-btn"
            />
            <span className="counter">{value}</span>
            <button
              onClick={() =>
                this.enhanceAmount({
                  title,
                  max,
                  lock,
                  available,
                })
              }
              className="counter-btn up"
            />
          </div>
        </ParamContent>
        <div className="param-text-cont">
          <span className="param-days">{days} days</span>
          <span className="param-price medium">$ {calculatedPrice}</span>
        </div>
      </ParamWrap>
    );
  }
}
