import React from 'react';
import PropTypes from 'prop-types';
import Img from 'gatsby-image';
import HtmlFromMarkdown from 'components/htmlFromMarkdown';
import { Section, Title, ArticleImage, Button } from './article.css';

const Article = ({ image, title, content, href, btnBorder }) => (
  <Section>
    <ArticleImage>
      <a href={href}>
        <Img fluid={image ? image.childImageSharp.fluid : {}} alt={title} />
      </a>
    </ArticleImage>
    <div className="article-text">
      <Title as="h4">
        <a href={href}>{title}</a>
      </Title>
      <HtmlFromMarkdown markdown={content} />
      <Button btnBorder={btnBorder}>
        READ
      </Button>
    </div>
  </Section>
);

Article.propTypes = {
  image: PropTypes.shape({
    childImageSharp: PropTypes.object.isRequired,
  }).isRequired,
  title: PropTypes.string.isRequired,
  content: PropTypes.shape({
    childMarkdownRemark: PropTypes.object.isRequired,
  }).isRequired,
  btnBorder: PropTypes.bool,
};

Article.defaultProps = {
  btnBorder: null,
};

export default Article;
